<script>
export default {
  name: '',
  data () {
    return {
      t: null,
      index: 2
    }
  },
  methods: {
    searchInit () {
      let searchBox = document.querySelector('.search-box')
      let bannerBox = document.querySelector('.jd-banner')

      window.onscroll = function () {
        let top = document.body.scrollTop || document.documentElement.scrollTop
        let height = bannerBox.offsetHeight
        let opacity = 0
        if (top > height) {
          opacity = 0.8
        } else {
          opacity = 0.8 * (top / height)
        }
        searchBox.style.background = `rgba(216, 80, 92, ${opacity})`
      }
    },
    bannerInit () {
      this.t = setInterval(() => {
        document.querySelector(`.jd-banner ul:last-child li:nth-child(${this.index})`).click()
        if (this.index++ % 8 === 0) {
          this.index = 1
        }
      }, 3000)
    },
    clickHandler (e, n) {
      document.querySelector('.jd-banner ul:first-child').style.transform = `translateX(${n * -12.5}%)`
      document.querySelector('.jd-banner ul:last-child li.current').classList.remove('current')
      event.target.classList.add('current')
    },
    mouseOver () {
      clearInterval(this.t)
    },
    mouseOut () {
      this.t = setInterval(() => {
        document.querySelector(`.jd-banner ul:last-child li:nth-child(${this.index})`).click()
        if (this.index++ % 8 === 0) {
          this.index = 1
        }
      }, 3000)
    },
    downTimeInit () { }
  },
  mounted () {
    this.searchInit()
    this.bannerInit()
    this.downTimeInit()
  },
}
</script>

